import Highcharts from '../../static/highcharts.js'

export default {
	name: "Points",
	data() {
		return {
			state: this.$store.state.point_state,
			item: ["固定积分", "日常奖扣", "其他积分"],
			item_1: ["实产值", "虚产值", "创富产值"],
			year: this.$store.state.view_year,
			month: this.$store.state.view_month - 1,
			StringMonth: "",
			dataString: "",
			res: {},
			stateIndex: 1,
			daily: 0,
			fixed: 0,
			other: 0

		}
	},
	mounted() {
		var _this = this
		this.toString()
		if(this.state == true) {
			this.stateIndex = 1
		} else {
			this.stateIndex = 2
		}

		this.getData(this.stateIndex)

	},
	methods: {
		changeType(text) {
			var _this = this
			if(text == "积分") {
				this.state = true
				this.stateIndex = 1
				this.$store.state.point_state = this.state
				setTimeout(function() {
					_this.draw()
				})
			} else {
				this.state = false
				this.stateIndex = 2
				this.$store.state.point_state = this.state
				setTimeout(function() {
					_this.draw_price()
				})

			}
			this.getData(this.stateIndex)
		},
		getData(num) {
			var _this = this
			var data = {
				"n": "UserInfo",
				"s": this.$store.state.sessionId,
				"q": {
					"a": num,
					"time": this.dataString

				}
			}
			$.ajax({
				type: "post",
				url: _this.$store.state.src + "cis/UserInfo",
				data: data
			}).done(function(res) {
				console.log(res)
				_this.res = res.q.point
				if(num == 1) {
					if(res.q.point.percentage.daily == 0 && res.q.point.percentage.fixed == 0 && res.q.point.percentage.other == 0) {
						_this.daily = 33.3;
						_this.fixed = 33.3;
						_this.other = 33.3;
					} else {
						_this.daily = res.q.point.percentage.daily
						_this.fixed = res.q.point.percentage.fixed
						_this.other = res.q.point.percentage.other

					}
					setTimeout(function() {

						_this.draw()

					}, )

				} else {
					setTimeout(function() {

						_this.draw_price()

					}, )
				}

			});

		},

		last() {
			this.month -= 1
			if(this.month <= 0) {
				this.year -= 1
				this.month = 12
			}
			this.toString()

			this.getData(this.stateIndex)
		},
		next() {
			this.month += 1
			if(this.$store.state.year <= this.year) {
				if(this.month >= this.$store.state.month - 1) {
					this.month = this.$store.state.month - 1
				}
			}
			if(this.month > 12) {
				this.month = 1
				this.year += 1
			}

			this.toString()
			this.getData(this.stateIndex)
		},

		toString() {
			if(this.month < 10) {
				this.StringMonth = "0" + this.month
			} else {
				this.StringMonth = this.month
			}
			this.dataString = this.year + "-" + this.StringMonth
		},

		draw() {
			var chart = null;
			var _this = this;
			$('#container').highcharts({
				chart: {
					plotBackgroundColor: null,
					plotBorderWidth: null,
					plotShadow: false,

				},
				title: {
					floating: true,
					useHTML: true,
					text: "<div style='z-index:-1'><p style='font-size:0.36rem;text-align:center;'>总分值</p><p style='font-size:0.45rem;color:#3495fa;text-align:center;line-height:0.4rem'>" + _this.res.totalScore + "</p><p style='font-size:0.28rem;text-align:center;</div>'>奖分:" + _this.res.prizeScore + "</p><p style='font-size:0.28rem;text-align:center;'>扣分:" + _this.res.reduceScore + "</p>"
				},
				credits: {
					enabled: false
				},
				tooltip: {
					backgroundColor: "#FFFFFF",
					zIndex: 9,
					useHTML: true,
					headerFormat: null,
					padding: 0,
					borderWidth: 2,
					headerFormat: '<div class="tooltip">',
					footerFormat: '</div>',
					pointFormat: ' <span>{point.name}:{point.percentage:.1f}%</span>'

				},
				plotOptions: {
					pie: {
						size: _this.$store.state.clientWidth / 375 * 160,
						innerSize: _this.$store.state.clientWidth / 375 * 110,
						allowPointSelect: true,
						cursor: 'pointer',
						dataLabels: {
							enabled: true,
							connectorPadding: 0,
							useHTML: true,
							zIndex: -1,
							format: '<p style="font-size:0.28rem">{point.name}:</p> <p style="font-size:0.28rem">{point.percentage:.1f} %</p>',
							color: '#707070',

							style: {
								color: (Highcharts.theme && Highcharts.theme.contrastTextColor) || 'black'
							}
						},
						states: {
							hover: {
								enabled: false
							}
						},

						point: { // 每个扇区是数据点对象，所以事件应该写在 point 下面
							events: {
								// 鼠标滑过是，突出当前扇区
								mouseOver: function() {

								},
								// 鼠标移出时，收回突出显示
								mouseOut: function() {

								},
								// 默认是点击突出，这里屏蔽掉
								click: function(e) {
									console.log(e.point.name)
									if(e.point.name == "固定积分") {
										_this.$router.push({
											path: '/Fixed_points'
										})
									} else if(e.point.name == "日常奖扣") {
										_this.$router.push({
											path: '/Change_points'
										})
									} else {
										_this.$router.push({
											path: '/Other_points'
										})
									}
									_this.$store.state.transfer_month = _this.month
									_this.$store.state.transfer_year = _this.year
									return false
								}
							}
						}
					}
				},
				series: [{
					type: 'pie',
					zIndex: -1,
					animation: true,
					data: [{
							name: '日常奖扣',
							y: _this.daily,
							color: "#44e0b6"
						},
						{
							name: '其他积分',
							y: _this.other,
							color: "#3495fa"
						},
						{
							name: '固定积分',
							y: _this.fixed,
							color: "#fb9d4a"
						}

					]
				}]
			}, function(c) {
				// 环形图圆心
				var centerY = c.series[0].center[1],
					titleHeight = parseInt(c.title.styles.fontSize);
				console.log(centerY)
				c.setTitle({
					y: centerY - 25 * _this.$store.state.clientWidth / 375
				});
				chart = c;
			});
		},

		draw_price() {
			var chart = null;
			var _this = this;
			$('#container_price').highcharts({
				chart: {
					plotBackgroundColor: null,
					plotBorderWidth: null,
					plotShadow: false,

				},
				title: {
					floating: true,
					useHTML: true,
					text: "<div style='z-index:-1'><p style='font-size:0.36rem;text-align:center;'>总产值</p><p style='font-size:0.45rem;color:#3495fa;text-align:center;line-height:0.4rem'>90</p>"
				},
				credits: {
					enabled: false
				},
				tooltip: {
					backgroundColor: "#FFFFFF",
					zIndex: 9,
					useHTML: true,
					headerFormat: null,
					padding: 0,
					borderWidth: 2,
					headerFormat: '<div class="tooltip">',
					footerFormat: '</div>',
					pointFormat: ' <span>{point.name}:{point.percentage:.1f}%</span>'

				},
				plotOptions: {
					pie: {
						size: _this.$store.state.clientWidth / 375 * 160,
						innerSize: _this.$store.state.clientWidth / 375 * 110,
						allowPointSelect: true,
						cursor: 'pointer',
						dataLabels: {
							enabled: true,
							connectorPadding: 0,
							useHTML: true,
							zIndex: -1,
							format: '<p style="font-size:0.28rem">{point.name}:</p> <p style="font-size:0.28rem">{point.percentage:.1f} %</p>',
							color: '#707070',

							style: {
								color: (Highcharts.theme && Highcharts.theme.contrastTextColor) || 'black'
							}
						},
						states: {
							hover: {
								enabled: false
							}
						},

						point: { // 每个扇区是数据点对象，所以事件应该写在 point 下面
							events: {
								// 鼠标滑过是，突出当前扇区
								mouseOver: function() {

								},
								// 鼠标移出时，收回突出显示
								mouseOut: function() {

								},
								// 默认是点击突出，这里屏蔽掉
								click: function(e) {
									console.log(e.point.name)
									if(e.point.name == "实产值") {
										_this.$store.state.price_title = "实产值"
										_this.$router.push({
											path: '/Price'
										})
									} else if(e.point.name == "虚产值") {
										_this.$store.state.price_title = "虚产值"
										_this.$router.push({
											path: '/Price'
										})
									} else {
										_this.$store.state.price_title = "创富产值"
										_this.$router.push({
											path: '/Price'
										})
									}
									_this.$store.state.transfer_month = _this.month
									_this.$store.state.transfer_year = _this.year
									return false
								}
							}
						}
					}
				},
				series: [{
					type: 'pie',
					zIndex: -1,
					animation: true,
					data: [{
							name: '实产值',
							y: 56,
							color: "#44e0b6"
						},
						{
							name: '虚产值',
							y: 24,
							color: "#3495fa"
						},
						{
							name: '创富产值',
							y: 20,
							color: "#fb9d4a"
						}

					]
				}]
			}, function(c) {
				// 环形图圆心
				var centerY = c.series[0].center[1],
					titleHeight = parseInt(c.title.styles.fontSize);
				console.log(centerY)
				c.setTitle({
					y: centerY * _this.$store.state.clientWidth / 375
				});
				chart = c;
			});
		},

		goEach(text) {
			var _this = this
			if(text == "固定积分") {
				this.$router.push({
					path: '/Fixed_points'
				})
			} else if(text == "日常奖扣") {
				this.$router.push({
					path: '/Change_points'
				})
			} else if(text == "其他积分") {
				this.$router.push({
					path: '/Other_points'
				})
			} else {
				this.$store.state.price_title = text
				this.$router.push({

					path: '/Price'
				})

			}
			this.$store.state.transfer_month = this.month
			this.$store.state.transfer_year = this.year
		},
		go_rank(num) {
			this.$store.state.rankIndex = num
			this.$router.push({
				path: '/Rank'
			})

		}

	}

}



