export default{
	name:"Ticket",
	data(){
		return {
			year: true,
			state:true
		}
	},
	mounted(){
		
	},
	methods:{
		changeType(text) {
		if(text == "本月") {
			this.year = true
		} else {
			this.year = false
		}
	},
	change(text){
		if(text=="我的奖票"){
			this.state=true
		}else{
				this.state=false
		}
	},
	go_detail(){
		this.$router.push({
										path: '/Ticket_detail'
									})
	}
	}
}



