export default{
	name:"Integral",
	data(){
		return {
			year: true,
			CheckIndex:0,
			IntegralIndex:0
		
		}
	},
	computed:{
		line(){
			if(this.CheckIndex==0){
				return {
					"left":"0"
				}
			}else if(this.CheckIndex==1){
					return {
					"left":"33.33%"
				}
			}else{
				return {
					"left":"66.66%"
				}
			}
		}
	},
	mounted(){
		
	},
	methods:{
			changeType(text) {
		if(text == "本月") {
			this.year = true
		} else {
			this.year = false
		}
	},
	changeIntegral(num){
		this.IntegralIndex=num
	},
	changeCheck(num){
		console.log(num)
		this.CheckIndex=num
	},
	add(){
		this.$router.push({
	    	path: '/Add'
			})
	},
	go_detail(){
		this.$store.state.Check_state="日常奖扣"
		
		this.$router.push({
										path: '/Check_detail'
									})
	},
	delect_detail(){
	this.$store.state.Check_state="回收站"
		
		this.$router.push({
										path: '/Check_detail'
									})
	
	}
	
	}
}



