export default {
	name: 'app',
	mounted() {
		var _this=this


var data={
					"n": "UserLogin",
					"s": "",
					"q": {
						"name": "本地测试004",
						"pwd": "123456",
						"code": "xixi"
					}
				}
	
		$.ajax({
			
				"type": "POST",
				"url": _this.$store.state.src+"cis/UserLogin",
				 dataType:'JSON',
				data:data
				}).done(function(res) {
					console.log(res)
			_this.$store.state.sessionId=res.q.user.sessionId		
					
				console.log(_this.$store.state.sessionId)
			});

		}
	}



