export default{
	name:"Check",
	data(){
		return {
			year: true,
			state:true,
			show:false
			
		}
	},

	mounted(){
		
	},
	methods:{
		changeType(text) {
		if(text == "本月") {
			this.year = true
		} else {
			this.year = false
		}
	},
	change(text){
		if(text=="待审核"){
			this.state=true
		}else{
				this.state=false
		}
	},
	go_detail(){
		this.$store.state.Check_state="我的审核"
		
		this.$router.push({
										path: '/Check_detail'
									})
	},
	showSearch(){
		this.show=!this.show
	}
	}
}



