
import Picker from 'vux/src/components/picker/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'


export default {

name: "Rank_detail",
components: {
Picker,
XHeader
},
data() {
return {
index: this.$store.state.rankIndex,
indexText:"年度",
years1: [],
oldYear: this.$store.state.year,
oldMonth: this.$store.state.month,
newYear: this.$store.state.year,
newMonth: this.$store.state.month,
years2: [],
transfer: {
fromyear: [this.$store.state.year + "", this.$store.state.month  + ""],
toyear: [this.$store.state.year + "", this.$store.state.month + ""]

},
fromBol: false,
toBol: false,
showSearch: false,
hideSearch: {
"height": "0rem",
"z-index": "-2"
},
show_search: {
"height": "1.8rem",
"z-index": "90"
},
month: this.$store.state.month,
StringMonth: "",
fromDate: "",
toDate: ""

}
},

mounted() {
this.toString()
this.fromDate = this.$store.state.year + "-" + this.StringMonth
this.toDate = this.$store.state.year + "-" + this.StringMonth


this.makeYear()
this.makeOldyear()
console.log(this.years1)
},
computed: {
computeStyle() {
if(this.showSearch) {
return this.show_search
} else {
return this.hideSearch
}

}
},
methods: {
toString() {
if(this.month < 10) {
this.StringMonth = "0" + this.month
} else {
this.StringMonth = this.month
}
},
makeYear() {
this.years2 = []
for(let i = this.oldYear; i <= this.$store.state.year; i++) {
let obj = {
name: i + '年',
value: i + "",
parent: 0
}
this.years2.push(obj)
}

for(let i = this.oldYear+1; i < this.$store.state.year; i++) {
for(let j = 1; j <= 12; j++) {
let name = ""

if(j < 10) {
name = "0" + j + "月"

} else {

name = j + "月"

}
let obj = {
name: name,
value: j + "",
parent: i + ""
}
this.years2.push(obj)
}
}

if(this.oldYear!=this.$store.state.year){
for(let i=this.oldMonth;i<=12;i++){
var name = ""

if(i < 10) {
name = "0" + i  + "月"

} else {

name = i  + "月"

}
let obj = {
name: name,
value: i + "",
parent: this.oldYear + ""
}
this.years2.push(obj)
}
}




for(let i = this.oldMonth; i <=this.$store.state.month; i++) {
var name = ""

if(i < 10) {
name = "0" + i  + "月"

} else {

name = i  + "月"

}

let obj = {
name: name,
value: i + "",
parent: this.$store.state.year + ""
}
this.years2.push(obj)
}



},
makeOldyear() {
this.years1 = [];
for(let i = 1988; i <= this.newYear; i++) {
let obj = {
name: i + '年',
value: i + "",
parent: 0
}
this.years1.push(obj)
}
for(let i = 1988; i < this.newYear; i++) {
for(let j = 1; j <= 12; j++) {
let name = ""

if(j < 10) {
name = "0" + j + "月"

} else {

name = j + "月"

}
let obj = {
name: name,
value: j + "",
parent: i + ""
}
this.years1.push(obj)
}
}
console.log(this.newMonth)
for(let i = 1; i <= this.newMonth; i++) {
var name = ""

if(i < 10) {
name = "0" + i  + "月"

} else {

name =i  + "月"

}

let obj = {
name: name,
value: i + "",
parent: this.newYear + ""
}
this.years1.push(obj)
}

},

change(index,text) {
this.index = index
this.indexText=text

},
search() {
this.showSearch = !this.showSearch
},
toChange(value) {
console.log(value)
this.transfer.toyear = value
},
fromChange(value) {
console.log(value)
this.transfer.fromyear = value

},
showDate(text) {
if(text == "from") {
this.fromBol = !this.fromBol
} else {
this.toBol = !this.toBol
}

},
closeYear() {
this.fromBol = false
this.toBol = false
},
comfirmFrom() {
var month = ""
if(Number(this.transfer.fromyear[1]) < 10) {
month = "0" + (Number(this.transfer.fromyear[1]))
} else {
month = Number(this.transfer.fromyear[1])
}
this.oldYear = Number(this.transfer.fromyear[0])
this.oldMonth = Number(this.transfer.fromyear[1])
this.makeYear()

this.fromDate = this.transfer.fromyear[0] + "-" + month
this.fromBol = false
},
comfirmTo() {
var month = ""
if(Number(this.transfer.toyear[1])< 10) {
month = "0" + (Number(this.transfer.toyear[1]))
} else {
month = Number(this.transfer.toyear[1])
}
this.newYear = Number(this.transfer.toyear[0])
this.newMonth = Number(this.transfer.toyear[1])
this.makeOldyear()






this.toDate = this.transfer.toyear[0] + "-" + month

this.toBol = false
},
nowDate(text) {

if(text == "from") {
this.fromDate = this.$store.state.year + "-" + this.StringMonth
} else {
this.toDate = this.$store.state.year + "-" + this.StringMonth
}
}
}
}



