export default{
	name:"Copy",
	data(){
		return {
			year: true,
			state:true,
			show:false,
			noData:false
		}
	},
	methods:{
		changeType(text) {
		if(text == "本月") {
			this.year = true
		} else {
			this.year = false
		}
	},
	go_detail(){
		this.$store.state.Check_state="抄送列表"
		this.$router.push({
										path: '/Check_detail'
									})
	},
	showSearch(){
		this.show=!this.show
	}
		
	}
}



