

import Swiper from 'vux/src/components/swiper/swiper.vue'

const imgList = [
"static/img/swiper-1.png",
"static/img/swiper-2.png",
"static/img/swiper-3.png",
"static/img/swiper-4.png"
]
const demoList = imgList.map((one, index) => ({
url: 'javascript:',
img: one
}))

export default {
name: 'Home',
components:{
Swiper
},
data () {
return {
demo03_list: demoList,
swiperImg:[

"static/img/swiper-1.png",
"static/img/swiper-2.png",
"static/img/swiper-3.png",
"static/img/swiper-4.png"
],

item:[


]
}
},
mounted(){
console.log(this.$store.state.clientWidth)
if(this.$store.state.isChecked){
this.item=[
{text:"我的积分",
img:"static/img/1.png"},
{text:"日常奖扣",
img:"static/img/2.png"},
{text:"我的审核",
img:"static/img/8.png"},
{text:"积分排名",
img:"static/img/3.png"},
{text:"我的奖票",
img:"static/img/4.png"},
{text:"奖扣任务",
img:"static/img/9.png"},
{text:"意见反馈",
img:"static/img/5.png"},
{text:"抄送列表",
img:"static/img/6.png"},
{text:"修改密码",
img:"static/img/7.png"}


]
}else{
this.item=[
{text:"我的积分",
img:"static/img/1.png"},
{text:"日常奖扣",
img:"static/img/2.png"},
{text:"积分排名",
img:"static/img/3.png"},
{text:"我的奖票",
img:"static/img/4.png"},

{text:"意见反馈",
img:"static/img/5.png"},
{text:"抄送列表",
img:"static/img/6.png"},
{text:"修改密码",
img:"static/img/7.png"}


]




}



},
methods:{

goEach(text){
if(text=="修改密码"){
this.$router.push({
path: '/ChangePassword'
})
}else if(text=="我的积分"){
this.$router.push({
path:'/Points'
})
}else if(text=="积分排名"){
this.$router.push({
path:'/Rank'
})
}
else if(text=="意见反馈"){
this.$router.push({
path:'/Suggest'
})
}else if(text=="抄送列表"){
this.$router.push({
path:'/Copy'
})
}
else if(text=="我的奖票"){
this.$router.push({
path:'/Ticket'
})
}else if(text=="奖扣任务"){
this.$router.push({
path:'/Task'
})
}else if(text=="我的审核"){
this.$router.push({
path:'/Check'
})
}else{
this.$router.push({
path:'/Integral'
})
}
}
}
}



