	export default {
		name: "Change_points",
		data() {
			return {
				activeIndex: 0,
				 month:this.$store.state.transfer_month,
				       StringMonth:""
			}
		},
		mounted() {
this.toString()
		},
		methods: {
			goEach(text) {
				if(text == '工作类') {
					this.activeIndex = 0
				} else if(text == '行为类') {
					this.activeIndex = 1
				} else {
					this.activeIndex = 2
				}
			},
			go_detail() {
				this.$router.push({
					path: '/Points_detail'
				})
			},
			toString() {
				if(this.month < 10) {
					this.StringMonth = "0" + this.month
				} else {
					this.StringMonth = this.month
				}
			}
		}
	}



