export default {
	name: "Rank",
	data() {
		return {
			index: this.$store.state.rankIndex,
			rank: 0,
			score: 0

		}
	},
	mounted() {

		this.getData(this.index + 1)

	},
	methods: {
		getData(num) {
			var _this = this
			var data = {
				"n": "UserRankDetails",
				"s": this.$store.state.sessionId,
				"q": {
					"a": num

				}
			}

			$.ajax({
				type: "post",
				url: _this.$store.state.src + "cis/UserRankDetails",
				dataType: 'JSON',
				data: data
			}).done(function(res) {
				console.log(res)
				_this.rank = res.q.rank.rank
				_this.score = res.q.rank.score
				var time_canvas = document.getElementById("time-graph-canvas");
				_this.drawMain(time_canvas, 70, "#2b3643", "#35414f", false);
				var per_canvas = document.getElementById("per-graph-canvas");
				var per = (14 - 3) / 14 * 70
				_this.drawMain(per_canvas, per, "#3495fa", "transparent", true);

			});

		},

		changeData(num) {
			console.log(num)
			this.index = num
			this.getData(num + 1)
		},
		goDetail() {
			this.$store.state.rankIndex = this.index
			this.$router.push({
				path: '/Rank_detail'
			})
		},
		drawMain(drawing_elem, percent, forecolor, bgcolor, bol) {
			/* 
			    @drawing_elem: 绘制对象 
			    @percent：绘制圆环百分比, 范围[0, 100] 
			    @forecolor: 绘制圆环的前景色，颜色代码 
			    @bgcolor: 绘制圆环的背景色，颜色代码 
			*/
			var context = drawing_elem.getContext("2d");
			var center_x = drawing_elem.width / 2;
			var center_y = drawing_elem.height / 2;
			var rad = Math.PI * 2 / 100;
			var speed = 0;

			// 绘制背景圆圈  
			function backgroundCircle() {
				context.save();
				context.beginPath();
				context.lineWidth = 64; //设置线宽  
				var radius = center_x - context.lineWidth;
				context.lineCap = "round";
				context.strokeStyle = bgcolor;
				context.arc(center_x, center_y, radius, 0, Math.PI * 2, false);
				context.stroke();
				context.closePath();
				context.restore();
			}

			//绘制运动圆环  
			function foregroundCircle(n) {
				context.save();
				context.strokeStyle = forecolor;
				context.lineWidth = 64;
				context.lineCap = "round";
				var radius = center_x - context.lineWidth;
				context.beginPath();
				context.arc(center_x, center_y, radius, Math.PI / 1.5, Math.PI / 1.05 + n * rad, false); //用于绘制圆弧context.arc(x坐标，y坐标，半径，起始角度，终止角度，顺时针/逆时针)  
				context.stroke();
				context.closePath();
				context.restore();
			}

			if(bol == false) {
				backgroundCircle();
				foregroundCircle(percent);
			} else {
				(function drawFrame() {
					window.requestAnimationFrame(drawFrame);
					context.clearRect(0, 0, drawing_elem.width, drawing_elem.height);
					backgroundCircle();

					foregroundCircle(speed);
					if(speed >= percent) return;
					speed += 2;
				}());
			}

			//执行动画  

		}

	}
}



