
import XCircle from 'vux/src/components/x-circle/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

export default {
name: "Task_detail",
components: {
XCircle,
Scroller
},
data() {
return {
strokeColor2: '#3495fa',
percent2: 40,
taskIndex: 0,
prizeIndex: 13,
buckleIndex: 13,
peopleIndex: 13,
yearIndex: 13
}
},
mounted() {

},
methods: {
go_each(num) {
this.taskIndex = num
},
chance(text, num) {
if(text == "奖分任务") {
this.prizeIndex = num
} else if(text == "扣分任务") {
this.buckleIndex = num
} else if(text == "人次任务") {
this.peopleIndex = num
} else {
this.yearIndex = num

}

}
}
}



