export default{
	name:"Fixed_points",
	data(){
		return {
			     month:this.$store.state.transfer_month,
			     year:this.$store.state.transfer_year,
			     StringMonth:"",
			     dataString:"",
			     score:0,
			     noData:false
		}
	},
	mounted(){
			this.toString()
			console.log(this.year+"-"+this.StringMonth)
			this.getData()
			if(this.score==0){
				this.noData=true
			}
			
	},
	methods:{
		toString() {
			if(this.month < 10) {
				this.StringMonth = "0" + this.month
			} else {
				this.StringMonth = this.month
			}
			this.dataString=this.year+"年"+this.StringMonth
		},
		getData(){
			
			
		var _this=this
				var data={
					"n": "UserPointDetail",
					"s": this.$store.state.sessionId,
					"q": {
						"a": 1,
						"time":this.year+"-"+this.month
						
						
					}
				}
			$.ajax({
			type:"post",
			url:_this.$store.state.src+"cis/UserPointDetail",
			data:data
		}).done(function(res){
			console.log(res)
			_this.score=res.q.s
			
	
			
			
			
		});
			
			
			
		}
	}
}



