
import Group from 'vux/src/components/group/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'

export default{
name:"Add",
components:{
Group, XNumber,XSwitch
},
data(){
return {
list:[""],
stringValue:"1",
title:"",
add:true
}
},
mounted(){
console.log(this.$http)
this.$http.get("https://www.xueguoguo.cn/api/v1/colleges/luqu/1",{

}).then(function(res){
console.log(res)
})






},
methods:{
go_event(){
this.$router.push({
path:'/Add_event'
})
},
change_add(){
this.add=!this.add
},
go_people(){
this.$router.push({
path:'/Add_people'
})
},
focus(){
this.$refs.input.focus()
},
submit(){
this.$router.push({
path:'/Submit'
})
},
add_event(){
this.list.push("")
},
delect(index){
this.list.splice(index,1)
}
}
}



