
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

export default{
name:"Check_detail",
components:{
Actionsheet
},
data(){
return {
show1:false,
show2:false,
show3:false,
state:this.$store.state.Check_state,
menus1:{
menu1:"通过",
menu2:"<span style='color:red'>驳回</span>"


},
menus2:{
menu1:"撤回"

},

menus3:{
menu1:"提交",
menu2:"<span style='color:red'>编辑</span>",
menu3:"<span style='color:red'>删除</span>"


}


}
},
mounted(){




},
methods:{
showAction(){
if(this.state=="日常奖扣"){
this.show2=true
}else if(this.state=="我的审核"){
this.show1=true
}else{
this.show3=true
}


},
click (key) {

}
}
}



