

import Vue from 'vue'
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import App from './App'
import Home from './components/Home'
import Integral from './components/Integral'
import Login from './components/Login'
import ChangePassword from './components/ChangePassword'
import Points from './components/Points'
import Fixed_points from './components/Fixed_points'
import Change_points from './components/Change_points'
import Other_points from './components/Other_points'
import Rank from './components/Rank'
import Points_detail from './components/Points_detail'
import Rank_detail from './components/Rank_detail'
import Suggest from './components/Suggest'
import Copy from './components/Copy'
import Ticket from './components/Ticket'
import Ticket_detail from './components/Ticket_detail'
import Task from './components/Task'
import Check from './components/Check'
import Check_detail from './components/Check_detail'
import Add from './components/Add'
import Add_event from './components/Add_event'
import Event from './components/Event'
import Add_people from './components/Add_people'
import Check_people from './components/Check_people'
import Submit from './components/Submit'
import Task_detail from './components/Task_detail'
import Price from './components/Price'
import AjaxPlugin from 'vux/src/plugins/ajax/index.js'

import Vuex from 'vuex'
import store from './vuex/store'

Vue.use(VueRouter)
Vue.use(Vuex)
Vue.use(AjaxPlugin)
console.log(Vue.http)
const routes = [{
path: '/',
component: Home
},
{
path: "/Integral",
name: 'Integral',
component: Integral

}, {
path: "/Login",
name: "Login",
component: Login

}, {
path: '/ChangePassword',
name: "ChangePassword",
component: ChangePassword
}, {
path: '/Points',
name: 'Points',
component: Points
}, {
path: '/Fixed_points',
name: 'Fixed_points',
component: Fixed_points
}, {
path: '/Change_points',
name: 'Change_points',
component: Change_points
}, {
path: '/Other_points',
name: 'Other_points',
component: Other_points
}, {
path: '/Rank',
name: 'Rank',
component: Rank
}, {
path: "/Points_detail",
name: 'Points_detail',
component: Points_detail
}, {
path: '/Rank_detail',
name: 'Rank_detail',
component: Rank_detail
}, {
path: '/Suggest',
name: 'Suggest',
component: Suggest
}, {
path: '/Copy',
name: 'Copy',
component: Copy
},{
path:'/Ticket',
name:'Ticket',
component:Ticket
},{
path:'/Ticket_detail',
name:'Ticket_detail',
component:Ticket_detail
},{
path:'/Task',
name:'Task',
component:Task
},{
path:'/Check',
name:'Check',
component:Check
},
{
path:'/Check_detail',
name:'Check_detail',
component:Check_detail
}
,
{
path:'/Add',
name:'Add',
component:Add
},
{
path:'/Add_event',
name:'Add_event',
component:Add_event
},
{
path:'/Event',
name:'Event',
component:Event
},{
path:'/Add_people',
name:'Add_people',
component :Add_people
},{
path:'/Check_people',
name:'Check_people',
component:Check_people
},{
path:'/Submit',
name:'Submit',
component:Submit
},{
path:'/Task_detail',
name:'Task_detail',
component:Task_detail
},{
path:'/Price',
name:'Price',
component:Price
}
]

const router = new VueRouter({
routes
})


console.log(window.location.host)

FastClick.attach(document.body)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
router,
store,
render: h => h(App)
}).$mount('#app-box')

router.beforeEach((to, from, next) => {
console.log('测试')
next()
})


