import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
let nowYear=new Date().getFullYear()
let nowMonth=new Date().getMonth()+1


const store = new Vuex.Store({
  // 定义状态
  state: {
   clientWidth:document.documentElement.clientWidth,
   year:nowYear,
   month:nowMonth,
 
   isChecked:true,
   price_title:"",
   point_state:true,
   view_month:nowMonth,
   view_year:nowYear,
   transfer_month:0,
   transfer_year:0,
   rankIndex:1,
   Check_state:"",
   sessionId:"",
   src:"http://192.168.1.119/api/"
  }
})

export default store


