import Highcharts from '../../static/highcharts.js'
export default{
	name:"Other_points",
	data(){
		return {
			         month:this.$store.state.transfer_month,
			         StringMonth:""
		}
	},
	mounted(){
	var _this = this
	this.toString()
	setTimeout(function() {
		_this.draw()
	})
	this.getData()
	},
	methods:{
		getData(){
				var _this=this
				var data={
					"n": "UserPointDetail",
					"s": this.$store.state.sessionId,
					"q": {
						"a": 2,
						"time":this.year+"-"+this.month
						
						
					}
				}
			$.ajax({
			type:"post",
			url:_this.$store.state.src+"cis/UserPointDetail",
			data:data
		}).done(function(res){
			console.log(res)
			_this.score=res.q.s
	
		});
			
			
			
			
			
			
			
		},
		
		
		draw() {
		var chart = null;
		var _this = this;
		$('#container').highcharts({
			chart: {
				plotBackgroundColor: null,
				plotBorderWidth: null,
				plotShadow: false,

			},
			title: {
				floating: true,
				useHTML: true,
				text: "<p style='font-size:0.28rem;text-align:center;'>当月出勤天数</p><p style='font-size:0.45rem;color:#000000;text-align:center;line-height:0.6rem'>90</p>"
			},
			credits: {
				enabled: false
			},
			tooltip: {
				backgroundColor: "#FFFFFF",
				zIndex: 9,
				useHTML: true,
				headerFormat: null,
				padding: 0,
				borderWidth: 2,
				headerFormat: '<div class="tooltip">',
				footerFormat: '</div>',
				pointFormat: ' <span>{point.name}:{point.percentage:.1f}%</span>'

			},
			plotOptions: {
				pie: {
					size: _this.$store.state.clientWidth / 375 * 140,
					innerSize: _this.$store.state.clientWidth / 375 * 100,
					allowPointSelect: true,
					cursor: 'pointer',
					dataLabels: {
						enabled: false,
						connectorPadding: 0,
						useHTML: true,
						zIndex: -1,
						format: '<p style="font-size:0.28rem">{point.name}:</p> <p style="font-size:0.28rem">{point.percentage:.1f} %</p>',
						color: '#707070',

						style: {
							color: (Highcharts.theme && Highcharts.theme.contrastTextColor) || 'black'
						}
					},
					states: {
						hover: {
							enabled: false
						}
					},

					point: { // 每个扇区是数据点对象，所以事件应该写在 point 下面
						events: {
							// 鼠标滑过是，突出当前扇区
							mouseOver: function() {

							},
							// 鼠标移出时，收回突出显示
							mouseOut: function() {

							},
							// 默认是点击突出，这里屏蔽掉
							click: function(e) {
							

								return false
							}
						}
					}
				}
			},
			series: [{
				type: 'pie',
				zIndex: -1,
				data: [{
						name: '出勤天数',
						y: 0,
						color: "#44e0b6"
					},{
						name: '未出勤天数',
						y: 30,
						color: "#cccccc"
					}

				]
			}]
		}, function(c) {
			// 环形图圆心
			var centerY = c.series[0].center[1],
				titleHeight = parseInt(c.title.styles.fontSize);
			console.log(centerY)
			c.setTitle({
				y: centerY - 5* _this.$store.state.clientWidth / 375
			});
			chart = c;
		});
	},
	toString() {
			if(this.month < 10) {
				this.StringMonth = "0" + this.month
			} else {
				this.StringMonth = this.month
			}
		}
	}
}



